/**
 *  Mijn Getal grid
 */
.mijngetal-grid {
  position: relative;
  width: 990px;
  margin: 0; }

.mijngetal-grid .column {
  width: 540px;
  margin-left: 15px; }

.ie .mijngetal-grid .column {
  width: 557px;
  margin-left: 15px; }

.mijngetal-grid .service-column {
  width: 388px;
  margin-left: 32px; }

.ie .mijngetal-grid .service-column {
  width: 388px;
  float: none;
  display: inline-block;
  *display: inline;
  padding: 0 15px 0 0; }

.ie .mijngetal-grid .service-column .component {
  width: auto; }

/**
 *  Mijn Getal components
 */
.mijngetal-grid h2 {
  clear: both; }

.mijngetal-grid .step-indicator {
  height: 62px; }

.mijngetal-grid .step-indicator li a {
  position: relative;
  z-index: 200; }

.mijngetal-grid .step-indicator li {
  width: 169px; }

.mijngetal-grid .step-indicator p {
  background: none;
  color: #c7c7c7;
  float: left;
  width: 169px;
  text-align: center;
  height: 20px; }

.mijngetal-grid .step-indicator p a {
  background: none;
  color: #c7c7c7;
  text-decoration: none; }

.mijngetal-grid .step-indicator p.current a,
.mijngetal-grid .step-indicator p.visited a {
  color: #EE7F00; }

.mijngetal-grid .step-indicator p.current {
  background: url(../../img/flextool/img/stepindicator-pointer.png) no-repeat scroll 79px 0 transparent;
  color: #EE7F00; }

.mijngetal-grid .step-indicator p.visited {
  color: #EE7F00; }

.ie .mijngetal-grid .step-indicator p {
  position: relative;
  top: -20px;
  padding-top: 40px;
  z-index: 1; }

.ie .mijngetal-grid .step-indicator p.step1 {
  background: url(../../img/flextool/img/stepindicator-ie.png) no-repeat left top; }

.ie .mijngetal-grid .step-indicator p.step1.current {
  background: url(../../img/flextool/img/stepindicator-ie.png) no-repeat left -75px; }

.ie .mijngetal-grid .step-indicator p.step3 {
  background: url(../../img/flextool/img/stepindicator-ie.png) no-repeat right top;
  width: 171px; }

.ie .mijngetal-grid .step-indicator p.step3.current {
  background: url(../../img/flextool/img/stepindicator-ie.png) no-repeat right -75px; }

.mijngetal-grid .mandatory-fields {
  color: #919294;
  font-size: 11px;
  margin-bottom: 3px;
  text-align: right; }

.mijngetal-grid .text-field label,
.mijngetal-grid .date-picker label,
.mijngetal-grid .select-field label {
  width: 260px; }

.mijngetal-grid .field.size5-date-picker {
  padding-bottom: 0; }

.mijngetal-grid .field.size5-date-picker > label {
  margin-bottom: 23px; }

.mijngetal-grid .field.size5-date-picker .custom-text-field {
  left: 260px; }

.mijngetal-grid .field.size5-date-picker .picker-icon {
  left: 452px; }

.mijngetal-grid .custom-select-box {
  width: 228px; }

.mijngetal-grid fieldset {
  /**border-top: 1px solid #CCC; not needed anymore : regressietest sprint 4 **/
  padding-top: 20px;
  padding-bottom: 0px;
  border-bottom: 0; }

.mijngetal-grid a.light-button span span {
  padding: 0 0 0 1px; }

.pensioen-vraag h4 {
  font-weight: bold;
  margin-bottom: 8px; }

.pensioen-vraag .left {
  width: 30%; }

.pensioen-vraag .link-list {
  padding-top: 5px;
  border-top: 1px solid #e5e5e5; }

.pensioen-vraag .link-list li {
  padding-bottom: 0; }

.mijngetal-grid .pensioen-vraag {
  background: url(../../img/flextool/img/pensioen-opbouw.gif) no-repeat 188px center;
  margin-left: 0;
  margin-right: 0; }

.mijngetal-grid .pensioen-vraag fieldset {
  float: right;
  width: 250px;
  padding: 0;
  margin: 0;
  border: none; }

.mijngetal-grid .pensioen-vraag .text-field label {
  width: auto;
  padding-top: 0;
  margin-bottom: 14px; }

.mijngetal-grid .pensioen-vraag .text-field label.hint {
  margin: 0 0 0 10px;
  font-style: normal; }

.mijngetal-grid .uw-gegevens h6 {
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid #ccc; }

.mijngetal-grid .uw-gegevens h6 a {
  float: right;
  color: #ee7f00;
  font-weight: normal;
  display: none; }

.mijngetal-grid .uw-gegevens table {
  border-bottom: 0;
  margin-bottom: 20px; }

.mijngetal-grid .uw-gegevens th {
  width: 65%; }

.mijngetal-grid .uw-gegevens th,
.mijngetal-grid .uw-gegevens td {
  font-weight: normal;
  border: 0;
  vertical-align: top;
  padding: 0; }

.mijngetal-grid .uw-gegevens .pensioenwensen {
  display: none; }

.mijngetal-grid div.slider-control.size1 {
  width: 155px; }

.mijngetal-grid .unit-bar {
  width: 174px !important; }

.mijngetal-grid .slider-text-field label {
  width: 215px; }

.mijngetal-grid .slider-text-field {
  height: auto;
  overflow: hidden; }

.ie7 .mijngetal-grid .submit {
  margin-top: -27px; }

/* Graph component */
.mijngetal-grid .bar-graph {
  height: 219px;
  position: relative;
  width: 330px;
  margin-bottom: 35px; }

.mijngetal-grid .bars {
  height: 200px;
  width: 280px;
  position: absolute;
  left: 29px;
  bottom: 1px; }

.mijngetal-grid .bars .bar {
  height: 200px;
  width: 90px;
  margin-right: 1px;
  float: left;
  position: relative; }

.mijngetal-grid .bars .bar div {
  position: absolute;
  width: 90px;
  text-align: center;
  color: #fff; }

.mijngetal-grid .bars .bar .inkomen {
  background: #f7b776; }

.mijngetal-grid .bars .bar .pensioen {
  background: #7cb0d6; }

.mijngetal-grid .bars .bar .aow {
  background: #73b39e; }

.mijngetal-grid .bars .bar .ev {
  background: #bde4f7; }

.mijngetal-grid .bars .bar .tekort {
  background: #ee0000;
  font-weight: bold; }

.mijngetal-grid .legend {
  position: absolute;
  bottom: -29px;
  left: 29px;
  list-style: none; }

.mijngetal-grid .legend li {
  float: left;
  list-style: none;
  padding: 0;
  background: none;
  width: 91px;
  color: #919294;
  font-size: 11px; }
  .mijngetal-grid .legend li:before {
    display: none; }

.mijngetal-grid .schedule-appointment {
  display: none;
  height: 46px;
  float: none;
  zoom: 1;
  position: relative; }

.mijngetal-grid .explanation {
  margin-bottom: 15px;
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 2px; }

.mijngetal-grid .explanation p {
  margin-bottom: 5px; }

.mijngetal-grid .shortage {
  font-family: 'NN Dagny Display',arial,helvetica,sans-serif;
  color: #ee0000;
  font-size: 16px; }

.mijngetal-grid .solution {
  line-height: 24px; }

.mijngetal-grid .solution span {
  font-family: 'NN Dagny Display',arial,helvetica,sans-serif;
  color: #ee7f00;
  font-size: 20px; }

.mijngetal-grid .glass {
  background: none repeat scroll 0 0 #000000;
  border-radius: 4px 4px 4px 4px;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 358px;
  z-index: 20; }

.mijngetal-grid .loader {
  background: url(../../img/flextool/img/ajax-loader.gif) no-repeat scroll left top transparent;
  height: 32px;
  left: 50%;
  margin-left: -16px;
  margin-top: -16px;
  position: absolute;
  top: 50%;
  width: 32px;
  z-index: 30; }

.ie .page .mijngetal-grid .service-component h2 {
  padding-top: 20px;
  margin-bottom: 10px;
  width: 100%; }

.mijngetal-grid .service-component .contact-list {
  width: 326px; }

.mijngetal-grid .service-component .contact-list li {
  display: inline-block;
  padding-top: 16px;
  width: 160px; }

.ie7 .mijngetal-grid .service-component .contact-list li {
  display: inline;
  padding-top: 16px;
  width: 160px; }
