.uw-gegevens {
  border: 1px solid #ccc;
  padding: 14px;
  border-radius: 4px; }
  .uw-gegevens th {
    text-align: left; }

.pensioen-vraag {
  border: 1px solid #ccc;
  padding: 14px;
  border-radius: 4px;
  margin-bottom: 30px;
  overflow: hidden;
  position: relative; }
  .pensioen-vraag:before, .pensioen-vraag:after {
    left: 32%;
    top: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none; }
  .pensioen-vraag:before {
    border-color: rgba(194, 225, 245, 0);
    border-left-color: #c2e1f5;
    border-width: 202px 0 202px 61px;
    margin-top: -201px; }
  .pensioen-vraag:after {
    border-color: rgba(136, 183, 213, 0);
    border-left-color: #fff;
    border-width: 200px 0 200px 60px;
    margin-top: -200px; }
  .pensioen-vraag .text-field {
    max-width: 100%;
    width: 100%; }
  .pensioen-vraag .left {
    float: left; }
  .pensioen-vraag .field.form-section {
    width: 50%;
    float: right; }
  .pensioen-vraag .form-section fieldset .grid-unit {
    width: 100%; }
    .pensioen-vraag .form-section fieldset .grid-unit + .grid-unit input {
      width: 80%; }

