.mijngetal-grid .loading {
  position: relative; }
  .mijngetal-grid .loading:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.5);
    z-index: 100; }
  .mijngetal-grid .loading:after {
    content: '';
    position: absolute;
    z-index: 101;
    top: 50%;
    left: 50%;
    margin-left: -16px;
    margin-top: -16px;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    border: 0.15rem solid #f1edeb;
    border-top-color: #ea650d;
    animation: rotate 1.5s infinite linear;
    /*transform: ;*/
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden; }

@keyframes rotate {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
