/** ------------~ A C C U M U L A T E D   P E N S I O N   D I A L O G ~------------- */
.accumulated-pension-dialog {
  width: 780px !important;
  max-width: 780px;
  height: 445px; }

.dialog-shadow {
  max-width: none !important; }

/**
 * Layout
 */
.accumulated-pension-dialog .layout {
  letter-spacing: -4px;
  word-spacing: -4px; }

.accumulated-pension-dialog .layout .navigation {
  vertical-align: top;
  display: inline-block;
  *display: inline;
  *zoom: 1;
  width: 315px; }

.accumulated-pension-dialog .layout .content {
  vertical-align: top;
  display: inline-block;
  *display: inline;
  *zoom: 1;
  width: 435px; }

/** -----------------------~ S T E P   N A V I G A T I O N ~------------------------ */
.step-navigation {
  margin: 15px 0 30px; }

.step-navigation li {
  padding: 0 0 1px;
  background: none;
  font-size: 13px;
  line-height: 35px;
  height: 35px; }

/** Links */
.step-navigation li a {
  text-decoration: none; }

.step-navigation li a .step {
  display: inline-block;
  padding-right: 10px;
  color: #333;
  background: url(../../img/flextool/img/step-navigation.png) no-repeat left -36px;
  text-align: center;
  width: 20px; }

.step-navigation li.first a .step {
  background-position: left top; }

.step-navigation li.last a .step {
  background-position: left -72px; }

/** Current item links */
.step-navigation li a:hover,
.step-navigation li a.current {
  color: #ea650d; }

.step-navigation li a.current .step {
  color: #fff;
  background-position: left -144px; }

.step-navigation li.first a.current .step {
  background-position: left -108px; }

.step-navigation li.last a.current .step {
  background-position: left -180px; }

/** Current item steps */
.steps {
  position: absolute;
  bottom: 20px;
  text-align: center;
  width: 435px;
  z-index: 100; }

.ie7 .steps {
  margin-left: 170px; }

.steps li {
  width: 10px;
  height: 10px;
  background: url(../../img/flextool/img/lb-step.gif) no-repeat 0 -30px;
  display: inline-block;
  padding: 0; }

.ie7 .steps li {
  float: left;
  margin-right: 4px; }

.steps li.current {
  background-position: 0 0; }
